/* Для экранов от 1200px */
@media (min-width:1200px){
    .shop{
        border: solid medium black;
        width: 60%;
        margin: 10px 20% 0px 20%;
    padding-top: 20px;
    }
    .card {
        border: 1px solid black;
        margin: 0px 10px 10px 10px;
        padding: 5px;
    }
    .title {
        color: green;
        font-weight: bold;
        margin: 0px;
    }
    .vitrine {
        display: grid;
        grid-template-columns: 1fr 1fr;;
    }
    h1{
        text-align: center;
        background-color: orangered;
        color: white;
        padding: 6px 0px;
    }
    .promo{padding: 5px;
    }
    input{
        margin-right: 5px;
    }
 }


    /* Для экранов до 480px */
@media (max-width:480px){
    .shop{
        border: solid medium black;
        width: 96%;
        margin: 10px 2% 0px 2%;
    padding-top: 20px;
    }
    .card {
        border: 1px solid black;
        margin: 0px 10px 10px 10px;
        padding: 5px;
        font-size: 12px;
    }
    .title {
        color: green;
        font-weight: bold;
        margin: 0px;
    }
    .vitrine {
        display: block;   
    }
    h1{
        text-align: center;
        background-color: orangered;
        color: white;
        padding: 6px 0px;
    }
    .promo{padding: 5px;
    }
    input{
        margin-right: 5px;
        width: 40%;
    }
    button{
        font-size: 11px;
    }
}